@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";


* {
    font-family: 'Myriad Pro';
    font-weight: 400;
    font-style: normal;
    color: $text-main-color;
    text-decoration: none;

    min-height: 0;
    min-width: 0;
}
button, a {
    cursor: pointer;
}
._error {
    transition: .2s;
    border-color: #e02222 !important;
}
.error-text {
    font-size: 15px !important;
    color: #e02222 !important;    
    display: none;
    padding-top: 10px;
}

#gant-table-popup {
    max-width: 1140px;
    width: 100%;

    img {
        max-width: inherit;
        width: 100%;

    }
}
#map-popup {
    max-width: 1024px;
}
#map {
    max-width: 1024px;
    width: 100%;
}
.fancybox-content {
    padding: 0;
}
.fancybox-slide--html .fancybox-close-small {
    background-color: $main-color;
    opacity: 1;
    padding: 5px;
}
.fancybox-button svg path {
    color: #fff;
}

.container {
    max-width: 1140px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}
.section__header {
    @include section-header();
}
.btn {
    @include page-btn();
}
.btn-tarnsparent {
    @include page-btn-transparent();
}
.main-color {
    color: $main-color;
}
[data-tabs] {
    cursor: pointer;
}

.headline {
    padding: 162px 0;
    background-image: url('../img/headline-bg.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(55, 56, 61, 0.7);

    .container {

    }
    .headline__wrapper {
        max-width: 699px;
    }
    .headline__header {
        font-style: normal;
        font-weight: 700;
        font-size: 50px;
        line-height: 140%;
        
        /* or 70px */
        text-transform: uppercase;
        
        color: #FFFFFF; 
        margin-bottom: 25px;
    }
    p {
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        
        /* or 28px */
        
        color: #FFFFFF;
        margin-bottom: 35px;        
    }
    .headline__controlls {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .proj-order {
        max-width: 263px;
        margin-right: 20px;
    }
    .get-consultation {
    }
}

