    .container {
        display: flex;
        justify-content: start;
    }
    .architect__text-wrap {
        margin-right: 22px;
        max-width: 459px;
        display: flex;
        justify-content: start;
        align-items: stretch;
        p {
            font-style: normal;
            font-weight: 300;
            font-size: 23px;
            line-height: 150%;
            
            /* or 34px */
            
