        background-image: url('../img/architect-bg.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

        display: flex;
        justify-content: start;
        align-items: flex-start;

        p {
            font-style: normal;
            font-weight: 300;
