.expectations {
    padding-bottom: 130px;

    .slick-dots {
        text-align: right;
        padding-right: 22px;
        box-sizing: border-box;
        bottom: 39px;
    }
    .slick-dots li button::before {
        content: '';
        width: 9px;
        height: 9px;
        background-color: #fff;
        opacity: 1;
    }
    .slick-dots li.slick-active button::before {
        background-color: $main-color;
    }
    .container {

    }
    .section__header {
        margin-bottom: 45px;
    }
    .expectations__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .expectations__slider__wrap {
        position: relative;
    }
    .expectations__slider__controls {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 100;

        display: flex;
        justify-content: start;
    }
    .expectations__slider__control {
        width: 38px;
        height: 38px;
        background: #37383D;
        outline: none;
        border: none;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        &:last-child {
            background-color: $main-color;
            img {
                transform: scale(-1);
            }
        }
        img {
            width: 14px;
            height: 23px;
            object-fit: cover;
        }
    }
    .expectations__slider {
        max-width: 753px;

    }
    .expectations__slide {
        display: flex;
        justify-content: start;
        align-items: stretch;
    }
    .expectations__slide__img-box {
        margin-right: 20px;

        img {
            display: block;

            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
    .expectations__slide__text-wrap {
        box-sizing: border-box;
        background: #44474A;
        padding: 54px 22px 39px;
        max-width: 270px;
    }
    .expectations__slide__text-wrap__headline {
        margin-bottom: 39px;
        p {
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 140%;
            /* or 20px */
            color: #FFFFFF; 
        }
    }
    .expectations__slide__text-wrap__header {
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
        text-transform: uppercase;
        
        color: #FFFFFF; 
        margin-bottom: 16px;       
    }
    .expectations__slide__text-wrap__info {
        p {
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 140%;
            /* or 20px */
            color: #FFFFFF;            
        }
    }
    .expectations__slide__text-wrap__sub-header {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        /* identical to box height, or 28px */
        color: #FFFFFF;     
        margin-bottom: 22px;   
    }
    .expectations__info-wrap {
        padding: 53px 22px 38px;
        box-sizing: border-box;
        background: #F3F3F3;
        max-width: 367px;
    }
    .expectations__info__header {
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
        text-transform: uppercase;
        
        color: #37383D;
        margin-bottom: 25px;
    }
    .expectations__info__list {

    }
    .expectations__info__list__item {
        display: flex;
        justify-content: start;
        align-items: center;

        &:not(:last-child) {
            margin-bottom: 25px;
        }
        img {
            width: 34px;
            height: 34px;
            margin-right: 20px;
        }
        span {
            
        }
    }
}

.request {
    background-color: #44474A;
    padding: 60px 0;
    .container {

    }
    .request__wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-bottom: 30px;
        align-items: center;
        column-gap: 20px;
        p {
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 140%;
            /* or 21px */
            color: #FFFFFF;  
            margin-top: 24px;          
        }
    }
    .request__heading {
        font-style: normal;
        font-weight: 700;
        font-size: 34px;
        line-height: 120%;
        
        /* or 41px */
        
        color: #FFFFFF;
        position:  relative;
        
        &::before {
            content: '';
            max-width: 105px;
            width: 100%;
            height: 2px;
            display: block;
            background-color: $main-color;
            margin-bottom: 24px;
        }
    }
    .request__form {
        display: flex;
        justify-content: start;
        align-items: flex-start;
        flex-wrap: wrap;
    }
    .request__form__input__wrap {
        max-width: 425px;
        width: 100%;
        margin-right: 20px;

        input {
            margin-bottom: 3px;
        }
    }
    .request__form__input {

        max-width: 425px;
        width: 100%;
        background: transparent;
        border: 1px solid #F1F1F1;
        outline: none;
        padding: 10px 15px;
        box-sizing: border-box;
        margin-right: 20px;
        color: #fff;

        &::placeholder {
            opacity: 1;
            color: #C4C4C4;
        }
    }
    .request__form__submit-wrap {
        max-width: 250px;
        span {
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;
            color: #FFFFFF;   
            
            a {
                font: inherit;
                text-decoration: underline;
                color: inherit;   

            }
        }
    }
    .request__form__submit {
       text-transform: initial; 
       font-style: normal;
       font-weight: 400;
       font-size: 15px;
       line-height: 140%;
       
       color: #FFFFFF; 
       margin-bottom: 13px;      
    }
}

.compound {
    padding: 60px 0 50px;
    .container {

    }
    .section__header {
        margin-bottom: 45px;
    }
    .compound__wrapper {
        margin-bottom: 34px;

        &:last-child p{
            max-width: 753px;
        }
        &:last-child .compound__wrapper__wrap{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 20px;
            align-items: start;          
        }
        p {
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 140%;
            
            /* or 21px */
            
            color: $text-main-color;
            margin-bottom: 25px;
            min-height: 70px;
        }
    }
    .compound__sub-heading {
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 135%;
        color: $text-main-color;
        margin-bottom: 34px;
    }
    .compound__wrapper__wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;

        
    }
    .compound__list {
        color: $text-main-color;
    }
    .compound__list__header {
        font-style: normal;
        font-weight: 600;
        font-size: 17px;
        line-height: 140%;  
        display: block; 
        margin-bottom: 20px;     
    }
    .compound__list__item {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        &::before {
            content: '';
            width: 9px;
            height: 9px;
            background-color: $main-color;
            display: inline-block;
            margin-right: 17px;
        }
        &:not(:last-child) {
            margin-bottom: 15px;
        }
        &._active {
            color: $main-color;
        }
    }
    .ac-trigger {
        &._active {
            color: $main-color;
        }
    }
    .compound__sub-list {
        margin-top: 10px;
        position: relative;
        &::before {
            content: '';
            height: 101.50%;
            width: 1px;
            background-color: $main-color;
            display: block;
            position: absolute;
            top: -15px;
            left: 4px;
        }
    }
    .compound__sub-list__item {
        font-style: normal;
        font-weight: 300;
        font-size: 15px;
        line-height: 140%;
        /* identical to box height, or 21px */
        color: $text-main-color;
        
        display: flex;
        justify-content: start;
        align-items: center;

       &:not(:last-child) {
           margin-bottom: 9px;
       }
       &::before {
           content: '';
           width: 43px;
           height: 1px;
           background-color: $main-color;
           display: block;
           margin-right: 11px;
           margin-left: 4px;
       } 
       &._active {
           color: $main-color;
       } 
    }
    .compound__content-wrap {
        position: relative;
    }
    .compound__content-wrap__img {
        max-width: 580px;
        max-height: 410px;
        width: 100%;
        height: 100%;
        object-fit: cover;
        margin-bottom: 35px;
    }
    .compound__content__tabs-content {
        opacity: 0;
        display: none;
        p {

        }
        &._active {
            opacity: 1;
            // display: block;
        }
    }
    .compound__content__tabs-content__header {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        
        color: #37383D;
        margin-bottom: 7px;   
        
        &::after {
            content: '';
            display: block;
            max-width: 97px;
            height: 2px;
            background-color: $main-color;
            margin-top: 7px;
        }
    }

    .compound__wrapper__wrap-inner {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(420px, 560px));
        gap: 34px;
        align-items: flex-start;

        img {
            width: 100%;
            object-fit: cover;
        }
    }
}

