.advantages {
    padding: 60px 0;
    .container {

    }
    .section__header {
        margin-bottom: 25px;
    }
    .advantages__text {
        font-style: normal;
        font-weight: 300;
        font-size: 15px;
        line-height: 140%;
        
        /* or 21px */
        text-align: center;
        
        color: $text-main-color;  
        margin-bottom: 66px;      
    }
    .advantages__list {
       display: grid;
       grid-template-columns: repeat(auto-fit, minmax(310px, 550px)); 
       gap: 20px;
       justify-content: center;
    }
    .advantages__list__item {
        background-image: url('../img/advantages1.jpg');
        background-image: url('../img/advantages2.jpg');
        background-image: url('../img/advantages3.jpg');
        background-image: url('../img/advantages4.jpg');
        background-image: url('../img/advantages5.jpg');
        background-image: url('../img/advantages6.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

        padding: 5px;
        box-sizing: border-box;
        position: relative;

        &:hover .advantages__list__item__overlay, &:hover .advantages__list__item__desc {
            opacity: 1;
            transform: translateY(0);
        }
        &:hover .advantages__list__item__num {
            color: #fff;
        }
        
    }
    .advantages__list__item__overlay {
        transition: opacity .3s ease-in;
        opacity: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: linear-gradient(284.68deg, #B34215 0%, rgba(179, 66, 21, 0.5) 102.39%);        
        z-index: 5;
    }
    .advantages__list__item__container {
        position: relative;
        z-index: 10;
        border: 1px solid #FFFFFF;
        padding: 25px;       
        box-sizing: border-box;
        height: 100%;
    }
    .advantages__list__item__wrapper {
        display: flex;
        justify-content: start;
        align-items: flex-start;
        margin-bottom: 20px;
    }
    .advantages__list__item__num {
        transition: color .3s ease-in;
        font-style: normal;
        font-weight: 700;
        font-size: 85px;
        line-height: 50px;
        text-transform: uppercase;
        
        color: transparent;
        -webkit-text-stroke: 1px #fff;
        margin-right: 20px;
    }
    .advantages__list__item__header {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        
        /* or 28px */
        
        color: #FFFFFF;        
    }
    .advantages__list__item__desc {
        transition: opacity .3s ease-in, transform .3s ease-in;
        opacity: 0;
        transform: translateY(10px);
        position: relative;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 120%;
        
        /* or 17px */
        
        color: #FFFFFF;
        max-width: 357px;
        margin-left: auto;
        padding-left: 24px;

        &::before {
            content: '';
            height: 95%;
            width: 2px;
            background-color: #fff;
            margin-right: 24px;
            position: absolute;
            left: 0;
            top: 0;
        }
    }
    .advantages__epilog {
        margin-top: 60px;
        background-color: $main-color;
        padding: 36px 0;

        .container {
            display: flex;
            justify-content: start;
            align-items: center;
            img {
                position: relative;
                top: -10px;
                margin-right: 30px;
            }
        }
    }
    .advantages__epilog__wrapper {
        max-width: 741px;
    }
    .advantages__epilog__heading {
        font-style: normal;
        font-weight: 700;
        font-size: 40px;
        line-height: 130%;
        /* or 52px */
        text-transform: uppercase;
        color: #FFFFFF;
        margin-bottom: 12px;
    }
    .advantages__epilog__link {
        display: block;
        background: #37383D;
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
        padding: 13px 0;
        max-width: 287px;
        width: 100%;

        text-align: center;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        
        text-transform: uppercase;
        color: #FFFFFF; 

        
    }
}

