        display: none;
    }
    .portfolio .portfolio__slider__control {
        display: none;
    }
    .faq {
        display: none;
    }
    .estimation {
        display: none;
    }
    .portfolio .section__header {
        margin-bottom: 290px;
    }
    .portfolio .portfolio__wrapper {
        flex-direction: column;
        position: relative;
    }
