    }
    .dream .dream__form {
        display: block;
    }
    .dream .dream__list {
        grid-template-columns: 1fr;
    }
    .dream .dream__contacts {
        display: block;
    }
    .dream .dream__map-link {
        display: none;
    }
    .dream .dream__text {
        font-size: 14px;
        line-height: 140%; 
        margin-bottom: 50px;
    }
    .dream .dream__header {
        font-size: 25px;
        line-height: 30px;
        margin-bottom: 15px;
    }
    .dream .dream__wrapper__desc {
        padding: 49px 33px 50px;
    }
    .dream .dream__wrapper img {
        display: none;
    }
    .about .about__desc p {
        margin-bottom: 25px;
    }
    .about .about__desc {
        margin-right: 0;
        margin-bottom: 50px;
    }
    .about .about__list {
        grid-template-columns: 1fr;
        row-gap: 25px;
    }
    .about .about__wrapper {
        flex-direction: column;
    }
    .about {
        padding: 50px 0;
    }
    .about .about__header {
        font-size: 25px;
        line-height: 30px;        
    }
    .working {
        padding: 0 0 50px;
    }
    .welcome {
        display: none;
    }
    .working .working__slider__slide__pb {
        display: none;
    }
    .working .working__slider__slide__miniheading {
        display: none;
    }
    .working__slider__slide__list__item {
        &:nth-child(2), &:nth-child(3) {
            display: none;
        }
    }
    .working .working__slider__slide__list {
        grid-template-columns: 1fr;
        row-gap: 20px;
    }
    .working .working__slider__header {
        font-size: 20px;
        line-height: 110%;   
        margin-bottom: 12px;     
    }
    .working .epilog {
        display: none;
    }
    .working .working__slider__desc {
        margin-bottom: 20px;
        font-size: 14px;
        line-height: 160%;        
    }
    .working .working__slider__slide__right {
        display: none;
    }
    .working .slider__counter__desc::after {
        width: 41px;
    }
    .working .slider__counter__desc {
        font-size: 13px;
        line-height: 140%;      
        padding: 0 0 5px;  
    }
    .working .working__wrap {
        flex-direction: row-reverse;
    }
    .advantages .advantages__epilog {
        display: none;
    }
    .advantages .advantages__list__item__container {
        padding: 0;
    }
    .advantages .advantages__list__item__header {
        color: #37383D;    
        font-size: 16px;
        line-height: 120%;            
    }
    .advantages .advantages__list__item__num {
        -webkit-text-stroke: 1px $main-color;
        font-size: 50px;
        line-height: 30px;        
    }
    .advantages .advantages__list__item {
        background-image: none!important;
    }
    .advantages .advantages__list__item__desc {
        display: none;
    }
    .advantages .advantages__list__item__overlay {
        display: none;
    }
    .quality .quality__btn {
        position: absolute;
        bottom: 0;
    }
    .quality img {
        margin-top: 0;
        margin-bottom: 32px;
    }
    .quality .quality__heading {
        font-size: 25px;
        line-height: 30px;
        margin-bottom: 30px;
    }
