    }
    .expectations .expectations__slide__text-wrap {
        max-width: unset;
    }
    .expectations .expectations__slide__text-wrap__info p {
        display: none;
    }
    .expectations .expectations__slide {
        flex-direction: column-reverse;
    }
    .compound .compound__wrapper__wrap {
        display: none !important;
    } 
    .compound .compound__wrapper {
        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }   
}
@media screen and (max-width: 420px) {
    .headline {
        padding: 142px 0;
    }
    .expectations .expectations__info__header {
        font-size: 20px;
        line-height: 24px;     
    }
    .compound .compound__wrapper,
    .compound .compound__wrapper p {
        margin-bottom: 0;
    }
    .compound .compound__sub-heading {
        font-size: 20px;
