        width: 100%;
    }
    .request .request__heading {
        font-size: 25px;
        line-height: 120%;       
    }
    .expectations {
        padding-bottom: 50px;
    }
    .expectations .expectations__slide__text-wrap__sub-header {
        margin-bottom: 0;
    }
    .expectations .expectations__slide__text-wrap {
        padding-bottom: 15px;
    }
    .architect .architect__text-wrap p {
        font-size: 16px;
        line-height: 150%;        
    }
    .headline .proj-order {
        margin-bottom: 15px;
    }
    .headline .headline__controlls {
        flex-direction: column;
    }
    .headline p {
        font-size: 16px;
        line-height: 140%;        
    }
    .headline .headline__header {
        font-size: 30px;
        line-height: 140%;    
        margin-bottom: 15px;    
    }
    .architect {
        padding: 54px 0 104px;
    }
