//Тут храняться миксины

@mixin test() {
    background-color: red;
}

@mixin section-header() {
    font-style: normal;
    font-weight: 700;
    font-size: 50px;
    line-height: 60px;
    text-transform: uppercase;
    text-align: center;
    
    color: #37383D;

    @media screen and (max-width: 740px) {
        font-size: 25px;
        line-height: 25px;        
    }
}

@mixin page-btn() {
    display: block;
    max-width: 366px;
    width: 100%;
    text-align: center;
    padding: 12px 0 9px;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #fff;
    text-transform: uppercase;

    background: $main-color;
    outline: none;
    border: none;

    cursor: pointer;

    transition: all .15s ease-in-out;

    &:hover {
        background: #DA6536;
        color: #FFFFFF;
    }

    @media screen and (max-width: 420px) {
        max-width: 100% !important;
        margin-right: 0 !important;
        margin-left: 0 !important;
    }
}

@mixin page-btn-transparent() {
    cursor: pointer;
    display: block;
    max-width: 357px;
    width: 100%;
    border: 2px solid #FFFFFF;
    padding: 12px 0 9px;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    
    color: #FFFFFF;  
    transition: all .15s ease-in-out;

    
    &:hover {
        background: #FFF;
        color: #B34215;
        border: 2px solid #FFF;        
    }
    
    @media screen and (max-width: 420px) {
        max-width: 100% !important;
        margin: 0;
    }
}